import React, { useState } from 'react';
import { Box, Grid } from "@mui/material";
import { info } from "../../info/Info";
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import Project from './Project';
import Modal from '@mui/material/Modal';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};

export default function Portfolio() {
    let [images, setImages] = useState([]);
    let [title, setTitle] = useState("");
    let [isApp, setIsApp] = useState("");
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
        setImages([]);
        setTitle("");
    };
    return (
        <>
            <Grid container display={'flex'} justifyContent={'space-around'} style={{ gap: 15, marginTop: 32 }}>
                <Project
                    key={info.portal.title}
                    title={info.portal.title}
                    image={info.portal.images[0]}
                    description={info.portal.description}
                    showMore={() => {
                        setImages(info.portal.images);
                        setTitle(info.portal.title);
                        setIsApp(info.portal.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.flex.title}
                    title={info.flex.title}
                    image={info.flex.images[6]}
                    description={info.flex.description}
                    showMore={() => {
                        setImages(info.flex.images);
                        setTitle(info.flex.title);
                        setIsApp(info.flex.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.claropay.title}
                    title={info.claropay.title}
                    image={info.claropay.images[3]}
                    description={info.claropay.description}
                    showMore={() => {
                        setImages(info.claropay.images);
                        setTitle(info.claropay.title);
                        setIsApp(info.claropay.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.keydok.title}
                    title={info.keydok.title}
                    image={info.keydok.images[0]}
                    description={info.keydok.description}
                    showMore={() => {
                        setImages(info.keydok.images);
                        setTitle(info.keydok.title);
                        setIsApp(info.keydok.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.c2a.title}
                    title={info.c2a.title}
                    image={info.c2a.images[16]}
                    description={info.c2a.description}
                    showMore={() => {
                        setImages(info.c2a.images);
                        setTitle(info.c2a.title);
                        setIsApp(info.c2a.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.mc.title}
                    title={info.mc.title}
                    image={info.mc.images[9]}
                    description={info.mc.description}
                    showMore={() => {
                        setImages(info.mc.images);
                        setTitle(info.mc.title);
                        setIsApp(info.mc.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.doku.title}
                    title={info.doku.title}
                    image={info.doku.images[6]}
                    description={info.doku.description}
                    showMore={() => {
                        setImages(info.doku.images);
                        setTitle(info.doku.title);
                        setIsApp(info.doku.isApp);
                        handleOpen();
                    }}
                />
                <Project
                    key={info.prescription.title}
                    title={info.prescription.title}
                    image={info.prescription.images[1]}
                    description={info.prescription.description}
                    showMore={() => {
                        setImages(info.prescription.images);
                        setTitle(info.prescription.title);
                        setIsApp(info.prescription.isApp);
                        handleOpen();
                    }}
                />
            </Grid>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="child-modal-title"
                aria-describedby="child-modal-description"
            >
                <Box sx={{ ...style, width: "80%" }}>
                    <h2 id="child-modal-title">{title}</h2>
                    <Carousel autoPlay interval={4000}>
                        {
                            images.map((item, i) => <Box component={'img'} src={item} alt={'mockup'}
                                sx={{
                                    objectFit: "contain",
                                    width: isApp? "40vw !important" : "90vw !important",
                                    '@media (min-width: 801px)': {
                                        width: isApp? "45vw !important" : "60vw",
                                        height: "70vh",
                                    },
                                    '@media (max-width: 800px)': {
                                        width: isApp? "45vw !important" : "60vw",
                                        height: "70vh",
                                    },
                                    '@media (max-width: 500px)': {
                                        width: isApp? "80vw !important" : "70vw",
                                        height: "70vh",
                                    },
                                }} />)
                        }
                    </Carousel>
                </Box>
            </Modal>
        </>
    );
};