import React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

export default function Project(props) {
    return (
        <Card sx={{ maxWidth: 345, background: localStorage.getItem('darkMode') === 'false'? "white" : "black", }} key={`${props.key}`} >
            <CardMedia
                sx={{ height: 140 }}
                image={props.image}
                title={`${props.key}`}
            />
            <CardContent sx={{ color: localStorage.getItem('darkMode') === 'false'? "black" : "white", }}>
                <Typography gutterBottom variant="h5" component="div">
                    {props.title}
                </Typography>
                <Typography variant="body2">
                    {props.description}
                </Typography>
            </CardContent>
            <CardActions>
                <Button size="small" onClick={props.showMore}>Ver ms</Button>
            </CardActions>
        </Card>
    );
};